'use client';

import React, { useState, useEffect } from 'react';
import { DashboardLayout } from '@/components/dashboard/dashboard-layout';
import { Button } from "@/components/ui/button";
import { Loader2, Navigation } from "lucide-react";
import dynamic from 'next/dynamic';
import 'leaflet/dist/leaflet.css';

// Define interfaces
interface Coordinates {
  latitude: number;
  longitude: number;
}

interface Event {
  id: string;
  name: string;
  location?: string;
  startDate?: string;
  coordinates?: Coordinates;
  category?: string;
  [key: string]: any;
}

// Sample data for testing if GraphQL isn't working
const sampleEvents: Event[] = [
  {
    id: '1',
    name: 'Tech Conference',
    location: 'New York Convention Center',
    startDate: '2025-05-15',
    coordinates: {
      latitude: 40.7128,
      longitude: -74.0060
    },
    category: 'CONFERENCE'
  },
  {
    id: '2',
    name: 'Coding Hackathon',
    location: 'NYU Tech Center',
    startDate: '2025-05-20',
    coordinates: {
      latitude: 40.7295,
      longitude: -73.9965
    },
    category: 'HACKATHON'
  }
];

// Dynamic import for Map component to avoid SSR issues
const MapComponent = dynamic(
  () => import('./mapcomponent'),
  { 
    ssr: false,
    loading: () => (
      <div className="w-full h-[500px] flex items-center justify-center bg-muted rounded-lg">
        <Loader2 className="h-8 w-8 animate-spin text-muted-foreground" />
      </div>
    )
  }
);

export default function MapsPage() {
  const [userCoordinates, setUserCoordinates] = useState<Coordinates | null>(null);
  const [isLoadingLocation, setIsLoadingLocation] = useState(false);
  const [selectedEvent, setSelectedEvent] = useState<Event | null>(null);
  
  // Get user's location
  const getUserLocation = () => {
    setIsLoadingLocation(true);
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition((position) => {
        setUserCoordinates({
          latitude: position.coords.latitude,
          longitude: position.coords.longitude
        });
        setIsLoadingLocation(false);
      }, (error) => {
        console.error("Error getting location:", error);
        setIsLoadingLocation(false);
      });
    } else {
      console.error("Geolocation not supported");
      setIsLoadingLocation(false);
    }
  };

  // Get location on initial load
  useEffect(() => {
    if (!userCoordinates && !isLoadingLocation) {
      getUserLocation();
    }
  }, []);

  return (
    <DashboardLayout>
      <div className="flex flex-col gap-4">
        <h1 className="text-2xl font-bold">Events Near Me</h1>
        <p className="text-muted-foreground">Discover events happening in your area</p>
        
        <div className="flex gap-2 mb-4">
          <Button 
            size="sm" 
            className="gap-1"
            onClick={getUserLocation}
            disabled={isLoadingLocation}
          >
            {isLoadingLocation ? (
              <Loader2 className="h-4 w-4 animate-spin" />
            ) : (
              <Navigation className="h-4 w-4" />
            )}
            {isLoadingLocation ? "Getting Location..." : "Update Location"}
          </Button>
        </div>
        
        {/* Map container with fixed dimensions */}
        <div className="w-full rounded-lg overflow-hidden border">
          <div style={{ height: "500px", width: "100%" }}>
            <MapComponent 
              userCoordinates={userCoordinates}
              events={sampleEvents}
              loading={false}
              onEventSelect={setSelectedEvent}
              setUserCoordinates={setUserCoordinates}
            />
          </div>
        </div>
        
        {/* Display selected event */}
        {selectedEvent && (
          <div className="p-4 border rounded-lg mt-4">
            <h2 className="text-xl font-bold">{selectedEvent.name}</h2>
            <p>{selectedEvent.location}</p>
            <p className="text-sm text-muted-foreground">
              {selectedEvent.startDate && new Date(selectedEvent.startDate).toLocaleDateString()}
            </p>
            <Button 
              className="mt-2"
              variant="outline"
              onClick={() => setSelectedEvent(null)}
            >
              Close
            </Button>
          </div>
        )}
      </div>
    </DashboardLayout>
  );
}